import React, { Component } from 'react';
import MiniChapter from './MiniChapter';
import dsclogo from './assets/images/dsclogo.png';
import shortlogo from './assets/images/shortlogo.png';
import cardImage from './assets/images/cardImage.png';
import './App.sass';
import data from "./data/list.json";

import SearchBar from 'material-ui-search-bar'
import { withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import ButtonBase from '@material-ui/core/ButtonBase';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseOutlined from '@material-ui/icons/CloseOutlined';

import EmailIcon from '@material-ui/icons/Email';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import InstagramIcon from '@material-ui/icons/Instagram';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import ShopIcon from '@material-ui/icons/Shop';
import YouTubeIcon from '@material-ui/icons/YouTube';
import PublicIcon from '@material-ui/icons/Public';



const styles = (theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  cardContainer: {
    flexGrow: 1,
    height: "100vh",
    position: "fixed",
    margin: "0",
    overflowY: "auto"
    // right: "0"
  }
});

class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      searchKey: "",
      gridSize: 12,
      cardSize: 3,
      infoCardOpen: false,
      gridSpacing: 6,
      focusCardData: []
    }
  }

  makeSearch = () => {
    const searchKey = this.state.searchKey;
    console.log(searchKey);
  }

  handleInfoCardOpen = (data) => {
    this.setState({ focusCardData: data })
    console.log(data)
    this.setState({ infoCardOpen: true })
    this.setState({ cardSize: 4 })
    this.setState({ gridSize: 8 })
    this.setState({ gridSpacing: 10 })
  }

  handleInfoCardClose = () => {
    this.setState({ infoCardOpen: false })
    this.setState({ cardSize: 3 })
    this.setState({ gridSize: 12 })
    this.setState({ gridSpacing: 6 })
  }

  makeMapUrl = (data) => {
    return "https://maps.google.com/maps?q=" + data.Campus + "," + data.City + "&t=&z=13&ie=UTF8&iwloc=&output=embed"
  }

  render() {
    const { classes } = this.props;
    return (
      <React.Fragment>
        <CssBaseline />
        <Grid container spacing={3}>
          <Grid item lg={this.state.gridSize} xs={12} style={{ padding: "0 auto !important" }}>
            <Container component="div" maxWidth="sm" id="logoContainer">
              <img src={dsclogo} alt="DSC logo"></img>
              <SearchBar
                value={this.state.searchKey}
                onChange={(newValue) => this.setState({ searchKey: newValue })}
                onRequestSearch={() => this.makeSearch()}
                style={{
                  margin: '2rem auto',
                  maxWidth: "100%",
                  borderRadius: "500px"
                }}
                classes={{
                  iconButton: "bluebtn"
                }}
                placeholder="Enter Campus Name"
              />
            </Container>
            <Container maxWidth="lg" component="div" style={{ marginTop: "6rem" }}>
              <Grid container spacing={this.state.gridSpacing}>
                {data.dscs.map((dsc, i) => (
                  <MiniChapter cardSize={this.state.cardSize} onClick={() => this.handleInfoCardOpen(dsc)} data={dsc} key={i} />
                )
                )}
              </Grid>
            </Container>
          </Grid>
          {
            this.state.infoCardOpen &&
            (
              <Grid item lg={4} xs={12} style={{ padding: "0 auto !important" }}>
                <Card className={classes.cardContainer} >
                  <CardHeader
                    avatar={
                      <Avatar src={shortlogo} />
                    }
                    action={
                      <IconButton aria-label="close" onClick={this.handleInfoCardClose}>
                        <CloseOutlined />
                      </IconButton>
                    }
                    title={this.state.focusCardData.Acronym}
                    subheader={this.state.focusCardData.Campus}
                  />
                  <CardMedia
                    style={{ height: 0, paddingTop: '56.25%' }}
                    image={cardImage}
                    title="lorem ipsum"
                  />
                  <CardContent>
                    <Typography color="textSecondary" variant="h6" component="h2">
                      Social Links
                    </Typography>
                    <IconButton aria-label="Email" href={this.state.focusCardData.Email}>
                      <EmailIcon />
                    </IconButton>
                    <IconButton aria-label="Website" href={this.state.focusCardData.Website}>
                      <PublicIcon />
                    </IconButton>
                    <IconButton aria-label="Facebook" href={this.state.focusCardData.Facebook}>
                      <FacebookIcon />
                    </IconButton>
                    <IconButton aria-label="Twitter" href={this.state.focusCardData.Twitter}>
                      <TwitterIcon />
                    </IconButton>
                    <IconButton aria-label="Facebook" href={this.state.focusCardData.Instagram}>
                      <InstagramIcon />
                    </IconButton>
                    <IconButton aria-label="Facebook" href={this.state.focusCardData.YouTube}>
                      <YouTubeIcon />
                    </IconButton>
                    <IconButton aria-label="Facebook" href={this.state.focusCardData.Linkedin}>
                      <LinkedInIcon />
                    </IconButton>
                    <IconButton aria-label="Email" href={this.state.focusCardData.DSCApp}>
                      <ShopIcon />
                    </IconButton>
                    <br></br>
                    <br></br>
                    <Typography color="textSecondary" variant="h6" component="h2">
                      Lead Details
                    </Typography>
                    <Card>
                      <Grid container spacing={2}>
                        <Grid item>
                          <ButtonBase className={classes.image}>
                            <img className={classes.img} alt="complex" src="/static/images/grid/complex.jpg" />
                          </ButtonBase>
                        </Grid>
                        <Grid item xs={12} sm container>
                          <Grid item xs container direction="column" spacing={2}>
                            <Grid item xs>
                              <Typography gutterBottom variant="subtitle1">
                                Standard license
                              </Typography>
                              <Typography variant="body2" gutterBottom>
                                Full resolution 1920x1080 • JPEG
                              </Typography>
                              <Typography variant="body2" color="textSecondary">
                                ID: 1030114
                              </Typography>
                            </Grid>
                            <Grid item>
                              <Typography variant="body2" style={{ cursor: 'pointer' }}>
                                Remove
                              </Typography>
                            </Grid>
                          </Grid>
                          <Grid item>
                            <Typography variant="subtitle1">$19.00</Typography>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Card>
                    <br></br>
                    <br></br>
                    <Card>
                      <div class="mapouter">
                        <div class="gmap_canvas">
                          <iframe
                            width="100%"
                            height="100%"
                            src={this.makeMapUrl(this.state.focusCardData)}
                            frameborder="0"
                            scrolling="no"
                            marginheight="0"
                            marginwidth="0"
                            title="abc"
                          ></iframe>
                        </div>
                      </div>
                    </Card>
                  </CardContent>
                  <Container>
                    {/* <Typography color="textSecondary" component="p">
                      The data on this page can be outdated or incorrect. Kindly contact us for corrections.
                    </Typography> */}
                  </Container>
                </Card>
              </Grid>
            )
          }

        </Grid>
      </React.Fragment>
    );
  }
}

export default withStyles(styles)(App);